import React from "react";

interface MyState {
  startValue: string,
  finishValue: string
}

class FileNames extends React.Component<{}, MyState> {
  constructor(props: any) {
    super(props);

    this.state = {
      startValue: '',
      finishValue: ''
    };
  }

  render() {
    const { finishValue } = this.state;
    return (
      <form>
        <div style={{ display: 'flex', flexDirection: 'row' }}>
          <div style={{ flexDirection: 'column', flexBasis: '50%' }}>
            <label className="form-label">–ò—Å—Ö–æ–¥–Ω—ã–π —Ç–µ–∫—Å—Ç</label>
            <textarea className="form-text-area"
                      placeholder="1 100%&#10;1 100%&#10;3&#10;3.jpg&#10;3.jpg&#10;4&#10;4.jpg"
                      onChange={this.updateStartValue.bind(this)}
                      rows={10}
            />
          </div>
          <div style={{ flexDirection: 'column', flexBasis: '50%' }}>
            <label className="form-label">–†–µ–∑—É–ª—å—Ç–∞—Ç</label>
            <textarea className="form-text-area"
                      placeholder="1 100%&#10;3.jpg&#10;4.jpg"
                      value={finishValue}
                      readOnly={true}
                      rows={10}
            />
          </div>
        </div>
        <div>–ú–æ–¥—É–ª—å –¥–ª—è —Å—Ç—Ä–∞–Ω–∏—Ü—ã –Ω–µ–∏—Å–ø–æ–ª—å–∑—É–µ–º—ã—Ö —Ñ–∞–π–ª–æ–≤. –ü–æ–∑–≤–æ–ª—è–µ—Ç –≤—ã—Ç–∞—â–∏—Ç—å –Ω—É–∂–Ω—ã–µ –Ω–∞–∑–≤–∞–Ω–∏—è —Ñ–∞–π–ª–æ–≤,
        –¥–æ –∫–æ—Ç–æ—Ä—ã—Ö —Å–ª–æ–∂–Ω–æ –¥–æ–±—Ä–∞—Ç—å—Å—è –∏–Ω–∞—á–µ.</div>
      </form>
    )
  }

  private async updateStartValue(event: any) {
    await this.setState({
      startValue: event.target.value
    });
    this.replaceText();
  }

  private replaceText() {
    const { startValue } = this.state;
    if (!startValue) {
      return;
    }
    const stringsArray = startValue.split('\n').filter(item => item.length > 0)
      .filter((_, index) => index % 2 === 1);
    const resultString = stringsArray.join('\n');
    this.setState({
      finishValue: resultString
    })
  }
}

export default FileNames;