import React from "react";

interface MyState {
  startValue: string,
  finishValue: string
}

class QuoteReplacement extends React.Component<{}, MyState> {
  constructor(props: any) {
    super(props);

    this.state = {
      startValue: '',
      finishValue: ''
    };
  }

  render() {
    const { finishValue } = this.state;
    return (
      <form>
        <div style={{ display: 'flex', flexDirection: 'row' }}>
          <div style={{ flexDirection: 'column', flexBasis: '50%' }}>
            <label className="form-label">–ò—Å—Ö–æ–¥–Ω—ã–π —Ç–µ–∫—Å—Ç</label>
            <textarea className="form-text-area"
                      placeholder='"Hello there"'
                      onChange={this.updateStartValue.bind(this)}
                      rows={10}
            />
          </div>
          <div style={{ flexDirection: 'column', flexBasis: '50%' }}>
            <label className="form-label">–†–µ–∑—É–ª—å—Ç–∞—Ç</label>
            <textarea className="form-text-area"
                      placeholder="¬´Hello there¬ª"
                      value={finishValue}
                      readOnly={true}
                      rows={10}
            />
          </div>
        </div>
        <div>–ú–æ–¥—É–ª—å –¥–ª—è –∑–∞–º–µ–Ω—ã –∫–∞–≤—ã—á–µ–∫. –î–æ–≤–æ–ª—å–Ω–æ –ø—Ä–æ—Å—Ç–æ–π (–Ω–µ —É—á–∏—Ç—ã–≤–∞–µ—Ç –≤–ª–æ–∂–µ–Ω–Ω–æ—Å—Ç—å).</div>
      </form>
    );
  }

  private async updateStartValue(event: any) {
    await this.setState({
      startValue: event.target.value
    });
    this.replaceText();
  }

  private replaceText() {
    const { startValue } = this.state;
    let resultString = startValue;
    let startQuote = true;
    if (!startValue) {
      return
    }
    while (resultString.indexOf('"') > -1) {
      resultString = resultString.replace('"', startQuote ? '¬´' : '¬ª');
      startQuote = !startQuote;
    }
    this.setState({
      finishValue: resultString
    })
  }
}

export default QuoteReplacement;